.jssh-container {
  display: flex;
  position: relative;
  flex-flow: row;
  height: 100%;
  width: 100%;
  max-height: 640px;
}

.arrowOverlay {
  pointer-events: none;
  position: absolute;
}

.jssh-pointerArrow {
  stroke: red;
  stroke-width: 2px;
}

.jssh-codeContainer {
  height: fit-content;
  font-size: 12px !important;
}

.jssh-sectionOuter {
  flex: 1;
  height: 100%;
  padding: 8px;
}

.jssh-section {
  height: 100%;
  padding: 8px !important;
  display: flex;
  flex-direction: column;
}

.jssh-codeBlock {
  margin-top: 16px;
  background: rgba(255,255,255,.7);
  padding: 16px;
}

.jssh-table {
  border-collapse: collapse;
  width: 100%;
}

.jssh-blockContainer {
  flex-grow: 1;
  display: flex;
  flex-direction: column-reverse;
}

.jssh-block {
  margin-top: 8px;
}

.jssh-highlightedLine {
  opacity: 1 !important;
}

.jssh-highlightedLine::before {
  position: absolute;
  content: "";
  display: inline-block;
  width: .6em;
  height: .6em;
  border-right: 0.3em solid red;
  border-top: 0.3em solid red;
  transform: rotate(45deg);
  margin-right: 0.5em;
  margin-left: -1em;
  margin-top: .5em
}

.jssh-lineNumber {
  opacity: .5;
  width: 28px;
  font-style: italic;
  display: inline-block;
}

.jssh-blockHeader {
  font-weight: normal;
  font-style: italic;
}

.jssh-table td {
  border: 1px solid lightgray;
  padding: 4px;
}
